<template>
  <section v-if="winGame === 1">
    <h1>GG Bro t'es le meilleur !</h1>
  </section>
  <section v-else-if="winGame === 2">
    <h1>T'as perdu grosse merde !</h1>
  </section>
</template>

<script>
export default {
  name: "Modal",
  props: {
    winGame: null,
  },
  async beforeMount() {
    console.log("je suis modal !");
    if (this.winGame === 1) {
      console.log("GG");
      window.navigator.vibrate([200, 100, 200]);
    } else if (this.winGame === 2) {
      console.log("LOSER");
      window.navigator.vibrate(200);
    }
  },
};
</script>

