<template>
  <section class="container">
    <div id="icon">
      <img
        id="image"
        :src="require(`../assets/img/${profilePic}`)"
        class="img-thumbnail"
        alt="logo"
      />
      <div id="playerId">
        <h1>{{ player.id }}</h1>
      </div>
    </div>
    <div id="listIcon">
      <form v-on:submit="modificon(pick)">
        <p>Choisi ton image de profil :</p>
        <input type="radio" name="icon" value="logo1.png" v-model="pick" />
        <img src="../assets/img/logo1.png" />

        <input type="radio" name="icon" value="logo2.png" v-model="pick" />
        <img src="../assets/img/logo2.png" />

        <input type="radio" name="icon" value="logo3.png" v-model="pick" />
        <img src="../assets/img/logo3.png" />

        <input type="radio" name="icon" value="logo4.jpg" v-model="pick" />
        <img src="../assets/img/logo4.jpg" />

        <input type="radio" name="icon" value="logo5.png" v-model="pick" />
        <img src="../assets/img/logo5.png" />
        <button type="submit" id="updateIcon">Changer de profile</button>
      </form>
    </div>
    <table class="blueTable">
      <thead>
        <tr>
          <th>Troph</th>
          <th>Date</th>
          <th>Type</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(value, i) in player.trophies" :key="i">
          <td>{{ value.id }}</td>
          <td>{{ getDate(value.date) }}</td>
          <td>{{ value.type }}</td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<script>
import { mapState, mapActions } from "vuex";
import game from "../api-client/game";
export default {
  name: "Profil",
  data() {
    return {
      profilPic: null,
      pick: "",
    };
  },
  computed: {
    ...mapState({
      player: "player",
    }),
    profilePic() {
      try {
        require(`../assets/img/${this.player.icon}`);
        return this.player.icon;
      } catch {
        return "player.png";
      }
    }
  },
  methods: {
    ...mapActions({
      setPlayerIcon: "SET_PLAYER_ICON",
    }),
    modificon(pick) {
      game.updateIcon(this.player.id, pick);
      this.setPlayerIcon(this.player.id);
    },
    getDate(timestamp) {
      let date = new Date(timestamp * 1000);
      let hours = date.getHours();
      let minutes = "0" + date.getMinutes();
      let seconds = "0" + date.getSeconds();

      let formattedTime =
        hours + ":" + minutes.substr(-2) + ":" + seconds.substr(-2);
      return formattedTime;
    },
  }
};
</script>

<style scoped>
#icon {
  display: inline-block;
}
#updateIcon {
  width: 60%;
  height: auto;
  background-color: #7892c2;
  cursor: pointer;
}
#image {
  margin-bottom: 10px;
  width: 60%;
  height: auto;
  display: inline-block;
  border: solid 2px;
}
#playerId {
  display: inline-block;
  margin-bottom: 50px;
}
#listIcon {
  visibility: hidden;
}
table.blueTable {
  border: 1px solid #f1f1f1;
  background-color: #ffffff;
  width: 60%;
  text-align: left;
  border-collapse: collapse;
}
table.blueTable td,
table.blueTable th {
  border: 1px solid #aaaaaa;
  padding: 3px 2px;
}
table.blueTable tbody td {
  font-size: 13px;
}
table.blueTable tr:nth-child(even) {
  background: #d0e4f5;
}
table.blueTable thead {
  background: #697884;
  background: -moz-linear-gradient(top, #5592bb 0%, #327cad 66%, #1c6ea4 100%);
  background: -webkit-linear-gradient(
    top,
    #5592bb 0%,
    #327cad 66%,
    #1c6ea4 100%
  );
  background: linear-gradient(to bottom, #5592bb 0%, #327cad 66%, #1c6ea4 100%);
  border-bottom: 2px solid #444444;
}
table.blueTable thead th {
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
  border-left: 2px solid #d0e4f5;
}
table.blueTable thead th:first-child {
  border-left: none;
}

table.blueTable tfoot {
  font-size: 14px;
  font-weight: bold;
  color: #ffffff;
  background: #d0e4f5;
  background: -moz-linear-gradient(top, #dcebf7 0%, #d4e6f6 66%, #d0e4f5 100%);
  background: -webkit-linear-gradient(
    top,
    #dcebf7 0%,
    #d4e6f6 66%,
    #d0e4f5 100%
  );
  background: linear-gradient(to bottom, #dcebf7 0%, #d4e6f6 66%, #d0e4f5 100%);
  border-top: 2px solid #444444;
}
table.blueTable tfoot td {
  font-size: 14px;
}
table.blueTable tfoot .links {
  text-align: right;
}
table.blueTable tfoot .links a {
  display: inline-block;
  background: #1c6ea4;
  color: #ffffff;
  padding: 2px 8px;
  border-radius: 5px;
}
</style>
